.leaderboardCon {
  margin-top: 25px;
  margin-bottom: 35px;
  .leaderboard {
    h1 {
      font-weight: 700;
      text-shadow: 0 0 1px var(--white), 2px 2px 2px rgba(206, 89, 55, 0);
      &:nth-child(1) {
        border-bottom: 2px solid var(--blue);
      }
    }
    button {
      position: absolute;
      top: 10px;
      right: 10px;
      width: 30px;
      height: 30px;
      span {
        position: absolute;
        width: 0px;
        height: 5px;
        border-radius: 25px;
        background-color: var(--blue);
        box-shadow: 0px 0px 3px 0px var(--blue);
        transform: rotate(0deg);
        animation: rotateIn 0.5s ease 0.4s forwards;
      }
      span:nth-child(2) {
        animation: rotateOut 0.5s ease 0.4s forwards;
      }
      @keyframes rotateIn {
        0% {
          width: 0px;
          transform: rotate(0deg);
        }
        50% {
          width: 30px;
          transform: rotate(0deg);
        }
        100% {
          width: 30px;
          transform: rotate(45deg);
        }
      }
      @keyframes rotateOut {
        0% {
          width: 0px;
          transform: rotate(0deg);
        }
        50% {
          width: 30px;
          transform: rotate(0deg);
        }
        100% {
          width: 30px;
          transform: rotate(-45deg);
        }
      }
    }
    table {
      border-collapse: collapse;
      margin: 15px 0 10px 0;
      tbody {
        tr {
          width: 90%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 5px 10px 0 10px;
          border-right: 2px solid var(--blue);
          border-left: 2px solid var(--blue);
          th {
            border-bottom: 2px solid var(--blue);
            text-align: center;
            text-shadow: 0 0 1px var(--white), 2px 2px 2px rgba(206, 89, 55, 0);
          }
          td {
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
              width: 30px;
              height: 25px;
            }
          }
          td:nth-child(1) {
            margin: 2px 5px 0 5px;
            width: 35px;
          }
          td:nth-child(2),
          th:nth-child(2) {
            width: 50%;
            text-overflow: ellipsis;
          }
          td:nth-child(3) {
            margin: 0 10px 0 5px;
          }
        }
      }
    }
  }
}
