.borderCon{
  opacity:0;
  border:8px solid #00b5ff;
  border:8px solid var(--sky-blue);
  height:100%;
  box-shadow:inset 0px 0px 30px 2px #00b5ff,0px 0px 8px 0px rgba(0,191,255,0);
  box-shadow:inset 0px 0px 30px 2px var(--sky-blue),0px 0px 8px 0px rgba(0,191,255,0);
  width:100vw;
  position:fixed;
  top:0;
  left:0;
  pointer-events:none;
  animation:fadeInBorder .5s ease forwards
}

.borderCon::before{
  opacity:0;
  border:8px solid #00b5ff;
  border:8px solid var(--sky-blue);
  height:100%;
  width:100vw;
  position:fixed;
  top:0;
  left:0;
  pointer-events:none;
  content:"";
  border-radius:20px;
  animation:fadeInBorder .5s .2s ease forwards
}

@media only screen and (min-width: 1024px){
  body{
    margin-right:.5em
  }

  .borderCon,.borderCon::before{
    width:calc(100vw - .5em);
    height:calc(100vh - 42px);
    bottom:0px;
    top:unset
  }
}

@keyframes fadeInBorder{
  0%{
    opacity:0
  }

  100%{
    opacity:1
  }
}

canvas{
